@import url("https://use.typekit.net/tli5fla.css");
/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
html{
	scroll-behavior: smooth;
}
.wp-block-hp-framer-svg-hp-framer-svg {
	font-size: 1em;
	background: #ffff001a;
	padding: 1em;

	&.dark-theme {
		background: #333;
		color: #fff;

		button {
			background: #555;
			color: #fff;
			border: 1px solid #777;
		}

		p {
			color: #ddd;
		}
	}
}

#hp-framer-svg{
	background-color: transparent;
	margin: 0;
	padding: 0;
	font-family: 'legitima', serif;
}


.viewport__placeholder--100vh{
  height: 100vh;
	border: 1px solid blue;
}

.scroll--cup{
  height: 200vh;
  width: 100%;
}
.scroll--cup__landing--section{
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.scroll--cup__landing--text--container{
	height: fit-content;
	width: 100%;
	border: 1px solid blue;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.scroll--cup__landing--text--container__title{
	margin: 0;
	font-size: clamp(100px, 10vw, 275px);
	color: transparent;
	position: relative;
	overflow: hidden;
	text-wrap: nowrap;
}
.scroll--cup__landing--text--container__title--span{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	color: black;
	text-wrap: nowrap;
}
.scroll--cup__landing--img--container{
	height: 100%;
	width: 100%;
	border: 1px solid blue;
	display: flex;
	align-items: center;
	justify-content: center;
}
.scroll--cup__product--section{
	height: 100vh;
	background-color: black;
}
.scroll--cup__img--container{
  z-index: 2;
  width: 40vw;
  position: relative;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}
.scroll--cup__img{
	display: none;
}
.scroll--cup__img.active{
	display: block;
	width: 100%;
}
.scroll--cup__title{
	text-align: center;
	overflow: hidden;
	position: relative;
	border: 1px solid red;
	height: 60px;
}
.scroll--cup__title--inner{
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	position: absolute;
	border: 1px solid red;
}


/*
* SVG Modal
*/
.hp--framer--svg{
	z-index: 10;
	height: 100vh;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 1px solid blue;
	animation: forwards 0.25s hideModal ease-out;
	animation-delay: 2.5s;
	background-color: #fff;
}
.hp--framer--svg__svg{
	width: 75px;
}
.hp--framer--svg__title{
	margin: 0;
	text-align: center;
	animation: forwards displayText 0.5s ease-out;
	animation-delay: 2s;
	opacity: 0;
}
.hp--framer--svg__subtitle{
	margin: 0;
	text-align: center;
	animation: forwards displayText 0.5s ease-out;
	animation-delay: 2s;
	opacity: 0;
}



@keyframes displayText {
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
@keyframes hideModal {
	0%{
		opacity: 1;
	}
	100%{
		opacity: 0;
		display: none;
	}
}
